import Mock from 'mockjs'

var Accounts = {
  'success': '@pick(["true","false"])',
  'errorNo': function () {
    var errorNo = 0
    if(this.success === 'false') {
      errorNo = '1'
    }
    return errorNo
  },
  'errorMsg': function () {
    var errorMsg = ''
    if(this.errorType === '1') {
      errorMsg = ''
    } else {
      errorMsg = ''
    }
    return errorMsg
  },
  'accounts|1-3': [
    {
      'accountId': '@id',
      'accountName': '@cname',
      'password': '@string("lower", 6)',
      'mobile': '@pick(["18302778898","18102238898","13802770898"])',
      'email': '@pick(["1830277@qq.com","2238898@163.com","1380898@162.com"])',
      'forbid|1-10': true,
      'lastLoginTime': '@datetime',
      'lastLoginIp': '@ip',
      'description': '@pick(["",""])',
      'createTime': '@datetime',
      'updateTime': '@datetime',
      'isDelete|1-10': true,
      'roles|1': [
        {
          'roleId': '@id',
          'name': '@pick(["", ""])',
          'description': '@capitalize("")'
        }
      ],
      'permissions|1-2': [
        {
          'parentId': '@id',
          'permissionId': '@id',
          'parentName': '@pick(["", "", ""])',
          'name': '@pick(["", "", ""])',
          'parentUrl': function () {
            var _url = ''
            if(this.parentName === '') {
              _url = '/plant'
            } else if (this.parentName === '') {
              _url = '/client'
            }else{
              _url = '/financial'
            }
            return _url
          },
          'url': function () {
            var _url = ''
            if(this.name === '') {
              _url = 'add'
            } else if (this.name === '') {
              _url = 'edit'
            } else {
              _url = 'remove'
            }
            return this.parentUrl + '/' + _url
          },
          'description': function () {
            return this.parentName + '/' + this.name
          }
        }
      ],
      'company': {
        'companyId': '@id',
        'name': '@pick(["", "",""])',
        'code': '@string("upper", 5)',
        'address': function () { return this.name.split('')[0] },
        'mobile': '@pick(["18802778898","18102238898","13802770898"])',
        'defaultCarNo': '@pick(["A","A","B"])',
        'brand': '@pick(["","",""])',
        'type': '@pick(["",""])',
        'serviceTime': '@time("A HH:mm:ss")',
        'email': '@email',
        'description': '@capitalize("")'
      }
    }
  ]
}

