import Vue from 'vue'

/**
 * [KimAjax ajax]
 * @method KimAjax
 * @param  {[type]} method          [description]
 * @param  {[type]} url             [description]
 * @param  {[type]} params          [description]
 * @param  {[type]} successCallback [description]
 * @param  {[type]} errorCallback   [description]
 */
export const KimAjax = (method, url, params, successCallback, errorCallback) => {
  Vue.http(
    {
      method: method || 'GET', // 
      url: url, // 
      data: params || {}, // URL
      headers: {'X-Requested-With': 'XMLHttpRequest'}, // 
      body: '', // 
      responseType: 'json', // 
      timeout: 10000, // 
      before: function () {}, // jQuerybeforeSend
      progress: function () {}, // ProgressEvent
      credentials: true, // 
      emulateHTTP: true, // PUT, PATCH, DELETEHTTP POSTX-HTTP-Method-Override webPUT, PATCH, DELETE
      emulateJSON: true // requestbodyapplication/x-www-form-urlencoded content type Webapplication/json
    }
  ).then(successCallback, errorCallback)
}

/**
 * localStorage
 */
export const setStore = (name, content) => {
	if (!name) return
	if (typeof content !== 'string') {
		content = JSON.stringify(content)
	}
	window.sessionStorage.setItem(name, content)
}

/**
 * localStorage
 */
export const getStore = name => {
	if (!name) return
	return window.sessionStorage.getItem(name)
}

/**
 * localStorage
 */
export const removeStore = name => {
	if (!name) return
	window.sessionStorage.removeItem(name)
}

/**
 * [cookies]
 * @method setCookie
 * @param  {[type]}  name  [cookies]
 * @param  {[type]}  value [cookies]
 * @param  {[type]}  days  [cookies]
 */
export const setCookie = (name, value, days) => {
  var Days = 30
  if (days) {
    Days = days
  }
  var exp = new Date()
  exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000)
  document.cookie = name + '=' + escape(value) + ';expires=' + exp.toGMTString()
}

/**
 * [cookies]
 * @method getCookie
 * @param  {[type]}  name [description]
 * @return {[type]}       [description]
 */
export const getCookie = name => {
  if (document.cookie.length > 0) {
    var start = document.cookie.indexOf(name + '=')
    if (start !== -1) {
      start = start + name.length + 1
      var end = document.cookie.indexOf(';', start)
      if (end === -1) end = document.cookie.length
      return unescape(document.cookie.substring(start, end))
    }
  }
  return ''
}

/**
 * [cookies]
 * @method delCookie
 * @param  {[type]}  name [description]
 * @return {[type]}       [description]
 */
export const delCookie = name => {
  var exp = new Date()
  exp.setTime(exp.getTime() - 1)
  var cval = getCookie(name)
  if (cval != null) {
    document.cookie = name + '=' + cval + ';expires=' + exp.toGMTString()
  }
}

/**
 * [CODE]
 * @method getDataFormLUP
 * @param  {[type]}       lookupDefineCode [code]
 * @return {[type]}                        [description]
 */
