<template>
  <div id="clientLevel">
    <el-row class="searchBox" :gutter="20">
      <el-col :span="6">
        <div class="grid-content">
          <span class="searchTitle"></span>
          <div class="searchInput">
            <el-input v-model="searchArg.clientInfo" class="input" placeholder=""></el-input>
          </div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content">
          <span class="searchTitle"></span>
          <div class="searchInput">
            <div class="block">
              <el-date-picker v-model="searchArg.startDate" type="datetime" placeholder=""></el-date-picker>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content">
          <span class="searchTitle"></span>
          <div class="searchInput">
            <div class="block">
              <el-date-picker v-model="searchArg.endDate" type="datetime" placeholder=""></el-date-picker>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content buttonBox">
          <el-button type="primary" icon="search" @click="search"> </el-button>
        </div>
      </el-col>
    </el-row>

    <el-row class="clientInfoBox">
      <el-col :span="24">
        <div class="grid-content">
          <div class="tableTitle">
            <el-button type="primary" icon="plus"  @click="addLevel = true"></el-button>
            <el-button type="danger" icon="delete"  @click="removeLevel"></el-button>
          </div>
          <el-table :data="levelTableData" border tooltip-effect="dark" style="width: 100%" :default-sort = "{prop: 'createTime', order: 'descending'}" @selection-change="handleSelectionChange">
            <el-table-column align="center" type="selection" width="55"> </el-table-column>
            <el-table-column align="center" prop="gradeName" label="" show-overflow-tooltip></el-table-column>
            <el-table-column align="center" prop="serviceDiscount" label="" show-overflow-tooltip> </el-table-column>
            <el-table-column align="center" prop="productDiscount" label="" show-overflow-tooltip> </el-table-column>
            <el-table-column align="center" prop="createTime" sortable label="" show-overflow-tooltip> </el-table-column>
            <el-table-column align="center" prop="updateTime" sortable label="" show-overflow-tooltip> </el-table-column>
            <el-table-column align="center" label="" show-overflow-tooltip>
              <template slots-scope="scope">
                <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="tablePages">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="10"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </div>
      </el-col>
    </el-row>

    <el-row class="clientInfoBox">
      <el-col :span="24">
        <div class="grid-content">
          <el-card class="box-card" :body-style="{ padding: '10px 20px' }">
            <div slot="header" class="clearfix" >
              <span style="line-height:28px;font-size:16px;"></span>
              <el-button style="float: right;" :plain="true" size="small" @click="showChart" v-text="showText"></el-button>
            </div>
            <div id="levelChart" class="levelChart"></div>
          </el-card>
        </div>
      </el-col>
    </el-row>

    <!--  -->
    <el-dialog title="" :visible.sync="addLevel" class="levelForm">
      <el-form :model="levelForm">
        <el-form-item label="" :label-width="formLabelWidth" prop="gradeName">
          <el-input v-model="levelForm.gradeName" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="serviceDiscount">
          <el-select v-model="levelForm.serviceDiscount" placeholder="">
            <el-option label="" value="9"></el-option>
            <el-option label="" value="8"></el-option>
            <el-option label="" value="7"></el-option>
            <el-option label="" value="6"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="productDiscount">
          <el-select v-model="levelForm.productDiscount" placeholder="">
            <el-option label="" value="9"></el-option>
            <el-option label="" value="8"></el-option>
            <el-option label="" value="7"></el-option>
            <el-option label="" value="6"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="levelForm.description" type="textarea" :rows="2" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancleLevel"> </el-button>
        <el-button type="primary" @click="submitLevel"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
var echarts = require('echarts');
require('echarts/lib/chart/pie');
require('echarts/lib/chart/funnel');
// 
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
require('echarts/lib/component/toolbox');

export default {
  name: 'clientLevel',
  data (){
    return {
      searchArg: {
        clientInfo: '',
        startDate: '',
        endDate: ''
      },
      levelTableData: [{
        gradeName: '',
        serviceDiscount: '',
        productDiscount: '',
        createTime: '',
        updateTime: ''
      }],
      levelForm: {
        gradeName: '',
        serviceDiscount: '',
        productDiscount: '',
        createTime: '',
        updateTime: '',
        description: ''
      },
      currentPage: 1,
      total: 0,
      multipleSelection: [],
      addLevel: false,
      formLabelWidth: '120px',
      showText: ' ',
      legendData: ['','','',''],
      seriesData: [
          {value:120, name:''},
          {value:100, name:''},
          {value:980, name:''},
          {value:850, name:''},
      ]
    }
  },
  created() {
    this.$http.get('/api/client/level').then((response) => {
      this.levelTableData = response.body.data
      this.total = response.body.data.length
    }, response => {
      // error callback
      alert("level")
    })
  },
  mounted() {
    $(".el-card__header").css("padding","5px 20px")
    this.drawChart('levelChart')
  },
  methods: {
    search() {
      alert(JSON.stringify(this.searchArg))
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    handleEdit(index, row) {
      this.levelForm = row
      this.addLevel = true
    },
    handleDelete(index, row) {
        alert(index + "--" + row)
    },
    cancleLevel() {
      this.levelForm = ''
      this.addLevel = false
    },
    submitLevel() {
      this.$http.post('/api/client/addLevel', this.levelForm).then((response) => {
        alert(JSON.stringify(response.body.data))
        alert(response.status)
        alert(response.statusText)
        this.cancleLevel()
      }, response => {
        // error callback
        alert("level")
        this.cancleLevel()
      });
    },
    removeLevel() {
      if(this.multipleSelection.length > 1){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }else{
        this.$message({
          type: 'warning',
          message: '!!',
          duration: 2000,
          showClose: true
        });
      }
    },
    handleSelectionChange(val) {
      //alert(JSON.stringify(val))
      this.multipleSelection = val;
    },
    showChart() {
      var $this = this;
      if(this.showText.indexOf(' ') !== -1){
        $this.showText = ' ';
        $(".el-card__body").slideUp();
      }else{
        $this.showText = ' ';
        $(".el-card__body").slideDown();
      }
    },
    drawChart(id) {
      // domecharts
      let myChart = echarts.init(document.getElementById(id));
      // 
      myChart.setOption({
          title : {
              text: '',
              subtext: '',
              x:'center'
          },
          tooltip : {
              trigger: 'item',
              formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
              x : 'center',
              y : 'bottom',
              data: this.legendData
          },
          toolbox: {
              show : true,
              feature : {
                  mark : {show: true},
                  dataView : {show: true, readOnly: true},
                  magicType : {
                    show: true,
                    type: ['pie', 'funnel'],
                    option: {
                      funnel: {
                        x: '25%',
                        width: '50%',
                        funnelAlign: 'left',
                        min: 0,
                        max: 100,
                        minSize: '0%',
                        maxSize: '100%',
                        sort: 'descending',
                        gap: 2,
                      }
                    }
                  },
                  restore : {show: true},
                  saveAsImage : {show: true}
              }
          },
          calculable : true,
          series : [{
              name: '',
              type: 'pie',
              radius : '55%',
              center: ['50%', '60%'],
              data: this.seriesData
            }]
      });
    }
  },
}

</script>

<style scoped>
.searchBox{margin: 15px 0;}
.searchBox .searchTitle{display:inline-block;min-width:60px;width:16%;height:36px;line-height:36px;float:left;font-size:14px;font-weight: 600;}
.searchBox .searchInput{display:inline-block;width:84%;}
.searchBox .searchInput .block{display:inline-block;width:100%;}
.el-date-editor.el-input{width:100%;}
.clientInfoBox{margin-bottom:20px;}

.tableTitle{margin:5px 0 20px 0;}
.tablePages{margin:10px 0;float: right;}
.el-select{width: 100%;}
.levelChart{height:300px;}
</style>



