<template>
  <div class="clearfix">
    <transition name="router-fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>

  import '@/service/mock'
  import { KimAjax, setStore, getStore, getDataFormLUPById } from '@/assets/js/utils'

  export default {
    data() {
      return {
        message: 'APP',
      }
    },
    beforeCreate: function () {
      let loadingInstance = ''
      console.group('beforeCreate =======App.vue========');
      console.log("%c%s", "color:red" , "el     : " + this.$el); //undefined
      console.log("%c%s", "color:red","data   : " + this.$data); //undefined
      console.log("%c%s", "color:red","message: " + this.message);
      loadingInstance = this.$loading({ 'fullscreen': true,'text': '...','body': true, });
      setTimeout(function(){
        loadingInstance.close();
      },2000)
    },
    created: function () {
      console.group('created =======App.vue========');
      console.log("%c%s", "color:red","el     : " + this.$el); //undefined
      console.log("%c%s", "color:red","data   : " + this.$data); //
      console.log("%c%s", "color:red","message: " + this.message); //
    },
    beforeMount: function () {
      console.group('beforeMount =======App.vue========');
      console.log("%c%s", "color:red","el     : " + (this.$el)); //
      console.log(this.$el);
      console.log("%c%s", "color:red","data   : " + this.$data); //
      console.log("%c%s", "color:red","message: " + this.message); //
    },
    mounted: function () {
      console.group('mounted ======App.vue=========');
      console.log("%c%s", "color:red","el     : " + this.$el); //
      console.log(this.$el);
      console.log("%c%s", "color:red","data   : " + this.$data); //
      console.log("%c%s", "color:red","message: " + this.message); //
    },
    beforeUpdate: function () {
        console.group('beforeUpdate =======App.vue========');
        console.log("%c%s", "color:red","el     : " + this.$el);
        console.log(this.$el);
        console.log("%c%s", "color:red","data   : " + this.$data);
        console.log("%c%s", "color:red","message: " + this.message);
    },
    updated: function () {
        console.group('updated =======App.vue========');
        console.log("%c%s", "color:red","el     : " + this.$el);
        console.log(this.$el);
        console.log("%c%s", "color:red","data   : " + this.$data);
        console.log("%c%s", "color:red","message: " + this.message);
    },
    beforeDestroy: function () {
        console.group('beforeDestroy =======App.vue========');
        console.log("%c%s", "color:red","el     : " + this.$el);
        console.log(this.$el);
        console.log("%c%s", "color:red","data   : " + this.$data);
        console.log("%c%s", "color:red","message: " + this.message);
    },
    destroyed: function () {
        console.group('destroyed =======App.vue========');
        console.log("%c%s", "color:red","el     : " + this.$el);
        console.log(this.$el);
        console.log("%c%s", "color:red","data   : " + this.$data);
        console.log("%c%s", "color:red","message: " + this.message)
    },
    watch: {

    },
  }

</script>

<style>
  .router-fade-enter-active, .router-fade-leave-active {
  	transition: opacity .3s;
  }
  .router-fade-enter, .router-fade-leave-active {
  	opacity: 0;
  }
</style>



