<template>
  <div id="home">

      <el-select v-model="value4" clearable placeholder="" size="small">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <br/>
      <hr/>
      <br/>

      <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
      <div style="margin: 15px 0;"></div>
      <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
        <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
      </el-checkbox-group>

      <br/>
      <hr/>
      <br/>

      <el-button
         type="primary"
         @click="openFullScreen"
         v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="">
         3 
      </el-button>

      <br/>
      <hr/>
      <br/>

      <el-collapse v-model="activeName" accordion>
        <el-collapse-item title=" Consistency" name="1">
          <div></div>
          <div></div>
        </el-collapse-item>
        <el-collapse-item title=" Feedback" name="2">
          <div></div>
          <div></div>
        </el-collapse-item>
        <el-collapse-item title=" Efficiency" name="3">
          <div></div>
          <div></div>
          <div></div>
        </el-collapse-item>
        <el-collapse-item title=" Controllability" name="4">
          <div></div>
          <div></div>
        </el-collapse-item>
      </el-collapse>

      <br/>
      <hr/>
      <br/>

      <el-popover
        ref="popover4"
        placement="right"
        width="400"
        trigger="click">
        <el-table :data="gridData">
          <el-table-column width="150" property="date" label=""></el-table-column>
          <el-table-column width="100" property="name" label=""></el-table-column>
          <el-table-column width="300" property="address" label=""></el-table-column>
        </el-table>
      </el-popover>

      <el-button v-popover:popover4>click </el-button>

      <br/>
      <hr/>
      <br/>



  </div>
</template>

<script>

const cityOptions = ['', '', '', ''];

export default {
  name: 'home',
  data (){
    return {
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value4: '',

      checkAll: true,
      checkedCities: ['', ''],
      cities: cityOptions,
      isIndeterminate: true,

      fullscreenLoading: false,

      activeName: '1',

      gridData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-03',
          name: '',
          address: ' 1518 '
        }],


    }
  },
  methods: {
    handleCheckAllChange(event) {
      this.checkedCities = event.target.checked ? cityOptions : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
    },

    openFullScreen() {
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
      }, 3000);
    },

  
  }
}

</script>

<style scoped>
#home{}

</style>



