/**
 * 
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: 
 */
	let baseUrl
	let routerMode
	const imgBaseUrl = 'http://47.75.131.186:8080'

	if (process.env.NODE_ENV === 'development') {
		baseUrl = 'http://47.75.131.186:8080'
		routerMode = 'hash'
	} else {
		baseUrl = 'http://47.75.131.186:8080'
		routerMode = 'hash'
	}

	export {
		baseUrl,
		routerMode,
		imgBaseUrl
	}



